body {
  background-color: #0052d0 !important;
}
.App {
  text-align: center;
}



/* Custtom Fonts */
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-Thin.ttf);
  font-weight: 100;
}
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-ExtraLight.ttf);
  font-weight: 200;
}
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-Light.ttf);
  font-weight: 300;
}
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-Medium.ttf);
  font-weight: 500;
}
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-SemiBold.ttf);
  font-weight: 600;
}
@font-face {
  font-family: Ibm;
  src: url(/public/fonts/IBMPlexSansArabic-Bold.ttf);
  font-weight: 700;
}

























